@font-face {
  font-family: RobotoCondensed;
  font-weight: 800;
  src: url(assets/roboto-condensed.light.ttf);
}
@font-face {
  font-family: EconomicaBold;
  font-weight: 800;
  src: url(assets/economica-bold.ttf);
}
body {
  text-align: center;
  font-size: 20px;
  padding: 2.5%;
  font-family: RobotoCondensed;
}
h1,
h2,
h3,
h4 {
  font-family: EconomicaBold;
}
.track {
  display: inline-block;
  position: relative;
  margin: 50px;
  width: 200px;
  height: 200px;
  cursor: pointer;
}
.track-image {
  width: 200px;
  height: 200px;
}
.track-text {
  position: absolute;
  width: 200px;
  color: black;
  border-radius: 4px;
  font-size: 0.9em;
  padding-top: 10px;
}
.track-icon {
  padding-left: 2px;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: black;
  opacity: 0.5;
  color: white;
  position: absolute;
  bottom: 75px;
  left: 75px;
}
.project {
  width: 20em;
  display: inline-block;
}
